.posts-div {
  width: 100%;
    border: 1px rgb(129, 106, 72);  
    padding: 10px;
    margin: 10px;
}
.posts-div img {
  width: 80%;
  height: 300px;
}
.post-img {
  width: 40%;
}
.posts-div h3 {
text-transform: capitalize;
  font-size: 18px;
  color: #3a3022;
  font-weight: 700;
  font-family: "Sacramento", cursive;
  margin: 15px;
}
.title {
  margin: 5px;
  font-family: "Sacramento", cursive;
}
.posts-div p {
    font-size: 18px;
    color: #3a3022;
    font-family: "Sacramento", cursive;
    margin: 12px;
}
.post-body{
  width: 60%;
  padding: 15px;
  margin: 5px;
}
.com-del {
  display: flex;
  justify-content: space-between;
} 
.delete-div {
  display: flex;
  justify-content: flex-end;
}
.post-div-inner {
  padding: 5px;
    margin: 5px;
    display: flex;
    border-bottom: 1px solid rgb(34, 30, 30);
}


.all-comment {
  padding: 15px;
  display: none;
  text-align: end;

}
.user-com {
  height: auto;
  margin: 5px;
  text-align: start;
  flex-direction: column;
  padding: 5px;
  display: flex;
  border-radius: 10px;
  background-color: rgba(221, 224, 227, 0.32);
 }
.all-comment.active {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.comments {
  text-align: end;
  cursor: pointer;
}
.user-com h4 {
  margin: 0;
  padding: 5px;
} 
.posts {
  display: flex;
    justify-content: center;
    margin: 5px;
    border-radius: 10px;
    background-color: rgba(56, 89, 97, 0.053) ;
    height: auto;

}
.date{
  display: flex;
  justify-content: space-between;
}