.profile {
  height: 550px;
  border-radius: 10px;
  padding: 15px;
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  background-color: rgba(56, 89, 97, 0.053);
  height: 550px;
}
.profile button {
  padding: 5px;
  margin: 5px;
  color: rgb(67, 67, 38);
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-family: "Sacramento", cursive;
  background-color: rgba(98, 158, 104, 0.151);
  cursor: pointer;
}
.profile button:hover {
  background-color: rgba(98, 158, 104, 0.295);
  transition: 0.3s;
}
.log-out {
  position: absolute;
  background-color: rgb(129, 106, 72);
  margin-bottom: 5px;
  padding: 5px;
  font-family: "Sacramento", cursive;
  margin: 10px;
  left: 40%;
  bottom: 2%;
  border-radius: 5px;
  /* bottom: 100%; */
  background: none;
  box-shadow: 0 5px 2px -1px rgb(129 106 72);
  border: 1px solid rgb(129, 106, 72);
}

.img-div {
  width: 90%;
  height: 80%;
  border-radius: 50%;
}

.edit-form {
  margin: 0 auto;
  width: 50%;
  display: flex;
  flex-direction: column;
}
.edit-form button {
  color: rgb(129, 106, 72);
  background: none;
  box-shadow: 0 5px 2px -1px rgb(129, 106, 72);
  border: 1px solid rgb(129, 106, 72);
}
.edit-form input {
  text-align: center;
}
.profile-div {
  display: flex;

  padding: 15px;
}
.edit-profile {
  position: relative;
}
.edit-icon {
  position: absolute;
  top: 0%;
  right: 0%;
}
.profile-info p {
  display: flex;
}
#fileInput {
  display: none;
}
#icon {
  width: 20px;
  cursor: pointer;
}
